import React, { useState } from "react";
import axios from '../../axiosConfig.js';
import googleLogo from "../../assets/google-cirkle-icon.png";
import facebookLogo from "../../assets/facebook-cirkle-icon.png";
import "../../styles/register.module.css";

const LoginForm = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const handleLogin = async () => {
    setError(""); //       

    //  email  
    if (!email || !password) {
      setError(" ,  email  .");
      return;
    }
    
    try {   

      //    
      const response = await axios.post('/api/login', { email, password });

      console.log(" !", response.data);

      //    localStorage ( API  )
      if (response.data.token) {
        localStorage.setItem("authToken", response.data.token);
        window.location.href = "/";
      }
      console.log(localStorage.getItem("authToken")); 
    } catch (err) {
      console.error(" :", err.response ? err.response.data : err.message);
      setError(" email  !");
    }
  };

  return (
    <div className="login-form-container">
      <h1 className="title-light">  </h1>
      <div className="social-btn">
        <button className="google-login-btn login-icon">
          <img src={googleLogo} alt="Google" />
        </button>
        <button className="facebook-login-btn login-icon">
          <img src={facebookLogo} alt="Facebook" />
        </button>
      </div>
      <p className="description-light"></p>
      <div>
      <input className="login-form-input"
        type="email" 
        placeholder="Email..." 
        value={email} 
        onChange={(e) => setEmail(e.target.value)} 
      />
      <input className="login-form-input"
        type="password" 
        placeholder=".." 
        value={password} 
        onChange={(e) => setPassword(e.target.value)} 
      />
      {error && <p style={{ color: "red" }}>{error}</p>}
      </div>
      
      <button className="login-form-button" onClick={handleLogin}>
          
      </button>
      <p className="description-light"> ?</p>
    </div>
  );
};

export default LoginForm;
