import React, { useState} from "react";
import { useNavigate, useLocation  } from "react-router-dom";
import googleLogo from "../assets/google-cirkle-icon.png";
import facebookLogo from "../assets/facebook-cirkle-icon.png";
import eyeOpen from "../icons/eye-open.png";
import eyeClosed from "../icons/eye-closed.png";
import "../styles/register.module.css";

const EmailPasswordForm = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const role = location.state?.role || "parent";
  
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");  
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [errors, setErrors] = useState({}); 

  const handleNext = async (e) => {
    e.preventDefault();
    let newErrors = {};

    if (!email || !password || !confirmPassword) {
      newErrors.general = "  ";
    }

    if (password.length < 8) {
      newErrors.password = "  !";
    }

    if (password !== confirmPassword) {
      newErrors.confirmPassword = "  !";
    }

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    try {
      const response = await fetch("http://127.0.0.1:8000/api/register", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Accept": "application/json", 
        },
        body: JSON.stringify({ email, password, password_confirmation: confirmPassword, role }),
      });

      const data = await response.json();

      if (!response.ok) {
        const data = await response.json();
        localStorage.setItem('userRole', role); //  
        localStorage.setItem('authToken', data.token); //  
      

        if (data.errors?.email?.[0] === "The email has already been taken.") {
          setErrors({ email: " email  " });
        } else {
          setErrors({ general: data.message || " " });
        }
        return;
      }
  
      navigate("/registration/email-confirmation", { state: { email } });
    } catch (error) {
      setErrors({ general: " '  " });
    }
  };
 
  return (
    <div className="email-form-container">
      <a onClick={() => navigate(-1)} className="back-button">
        <span className="back-text"></span>
        <span className="back-arrow"></span>
    </a>
      <h1 className="title-light-full-page"></h1>
      <p className="description-light">     ...</p>
      <button className="role-info-btn" disabled>
        {role === "parent" ? "" : ""}
      </button>
      
      <p className="description-light">     <br></br>    .</p>

      <div className="social-btn">
              <button className="google-login-btn login-icon">
                <img src={googleLogo} alt="Google" />
              </button>
              <button className="facebook-login-btn login-icon">
                <img src={facebookLogo} alt="Facebook" />
              </button>
            </div>
            <p className="description-light"></p>
      <form className="register-form" onSubmit={handleNext}>
        <input className="input-field"          
          type="email" 
          placeholder=" email..." 
          value={email} 
          onChange={(e) => setEmail(e.target.value)}
          required 
        />
        {errors.email && <p className="error-text">{errors.email}</p>}
        
      <div className="password-container">
        <input 
          className="input-field password-input"
          type={showPassword ? "text" : "password"}
          placeholder="..." 
          value={password} 
          onChange={(e) => setPassword(e.target.value)}
          required 
        /> 
         <button 
            type="button" 
            className="show-password-btn" 
            onClick={() => setShowPassword(!showPassword)}
          >
            <img src={showPassword ? eyeOpen : eyeClosed} alt="toggle visibility" />
          </button>
      </div>
          {errors.password && <p className="error-text">{errors.password}</p>}       
        
      <div className="password-container">
        <input 
          className="input-field password-input"
          type={showConfirmPassword ? "text" : "password"}
          placeholder="..." 
          value={confirmPassword} 
          onChange={(e) => setConfirmPassword(e.target.value)}
          required 
        />
        <button 
            type="button" 
            className="show-password-btn" 
            onClick={() => setShowConfirmPassword(!showConfirmPassword)}
          >
            <img src={showConfirmPassword ? eyeOpen : eyeClosed} alt="toggle visibility" />
          </button>
      </div>
        {errors.confirmPassword && <p className="error-text">{errors.confirmPassword}</p>}

        {errors.email && <p className="error-text">{errors.email}</p>}
        {errors.general && <p className="error-text">{errors.general}</p>}

        <button className="reg-form-button" type="submit"></button>
      </form>
    </div>
  );
};

export default EmailPasswordForm;
